/** @jsx jsx */
import { jsx } from 'theme-ui'
import { navigate } from "gatsby";
import { fadeInUp } from '../../../styles/animations';

const OutlineButton = ({
  scroll,
  scrollTo,
  navigateTo,
  buttonText,
  animation,
  transition
}) => (
  <button
    onClick={() => {
      if (scrollTo) {
        scroll.animateScroll(document.getElementById(scrollTo), null, {
          speed: 300,
        })
      } else if (navigateTo) {
        navigate(navigateTo)
      }
    }}
    sx={{
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      mb: '4rem',
      mr: '.5rem',
      width: 'fit-content',
      mx: 'auto',
      borderRadius: '30px',
      p: '.5rem .5rem .5rem 1rem',
      bg: 'transparent',
      borderStyle: 'solid',
      borderWidth: '1px',
      borderColor: 'primary',
      cursor: 'pointer',
      textDecoration: 'none',
      color: 'inherit',
      ':hover': {
        boxShadow: '0px 0px 0px 14px rgba(255, 10, 142, .1)',
      },
      transition: {transition},
    }}
    css={animation}
  >
  <div sx={{ mr: '1.5rem' }}>{buttonText}</div>
  <div
    sx={{
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      bg: 'primary',
      borderRadius: '50%',
      width: 30,
      height: 30,
    }}
  >
    â†’
  </div>
</button>)

export default OutlineButton
