/** @jsx jsx */
import { jsx } from 'theme-ui'
import theme from '../../../styles/theme';
import List from '../List';
import OutlineButton from '../Button/OutlineButton';

export const ContentPageSection = ({
  title,
  subtitle,
  body,
  list,
  visual,
  sideContent,
  alignVisualRight,
  htmlBody,
  bottomPadding,
  cta
}) => (
  <div sx={{ mb: '6rem' }}>
    {title && <h2
      sx={{
        mb: bottomPadding || '2rem',
        color: 'white' }}
    >
      {title}
    </h2>}
    {subtitle && <p sx={{...theme.styles.blockSubtitle, mb: '2rem'}}>{subtitle}</p>}
    {body && <div sx={{ mb: visual || sideContent ? '2rem' : 0, color: 'text' }}>{body}</div>}
    {htmlBody}
    {visual && !alignVisualRight && (
      <div sx={{ mr: '2rem', width: '35%', display: 'inline-block' }}>{visual}</div>
    )}
    <div sx={{
      display: 'inline-block',
      verticalAlign: 'top',
      width: '60%',
      color: 'text'
    }}>
      {sideContent && sideContent.map((content, i) => (
        <p key={i}>{content}</p>
      ))}
      {list && <List list={list} />}
    </div>
    {visual && alignVisualRight && (
      <div sx={{ ml: '2rem', width: '35%', display: 'inline-block' }}>{visual}</div>
    )}
    {cta && <OutlineButton {...cta} />}
  </div>
)
